.ReactModalPortal > div{ //applying style only to divs that are children of ReactModalPortal
    opacity: 0;
}

.ReactModalPortal .ReactModal__Overlay{
    align-items: center;
    display: flex;
    justify-content: center;
    transition: opacity 200ms ease-in-out;
    
}

.ReactModalPortal .ReactModal__Overlay--after-open{
    opacity: 1;
}

.ReactModalPortal .ReactModal__Overlay--before-close{
    opacity: 0;
}

.modal{
    background: #070e6093;
    color: white;
    max-width: 30rem;
    outline: none;
    padding: 3.2rem;
    text-align: center;
}

.modal-title {
    margin: 0 0 1.6rem 0;
  }
  
  .modal-body {
    font-size: 2rem;
    font-weight: 300;
    margin: 0 0 3.2rem 0;
    word-break: break-all;
  }